<template>
    <div class="box">
        <div class="leftImg">
            <div class="Img"></div>
        </div>
        <div class="content">
            <div class="deviceInfo">
                <div class="box_title">
                    <div class="c_wordColor word">设备监控</div>
                </div>
                <div class="deviceInfo_content">
                    <div class="deviceInfoItem">
                        <img src="../../../../assets/bigScreen/cockpit/t3d_device.svg" alt="" />
                        <div class="deviceInfoItem_text">
                            <div class="deviceInfoItem_text_title">设备总数</div>
                            <div class="c_wordColor deviceInfoItem_text_num">{{ deviceInfo.total }}</div>
                        </div>
                    </div>
                    <div class="deviceInfoItem">
                        <img src="../../../../assets/bigScreen/cockpit/t3d_waring.svg" alt="" />
                        <div class="deviceInfoItem_text">
                            <div class="deviceInfoItem_text_title">实时警告</div>
                            <div class="c_wordColor deviceInfoItem_text_num">{{ deviceInfo.alertNum }}</div>
                        </div>
                    </div>
                    <div class="deviceInfoItem">
                        <img src="../../../../assets/bigScreen/cockpit/t3d_online.svg" alt="" />
                        <div class="deviceInfoItem_text">
                            <div class="deviceInfoItem_text_title">在线设备</div>
                            <div class="c_wordColor deviceInfoItem_text_num">{{ deviceInfo.online }}</div>
                        </div>
                    </div>
                    <div class="deviceInfoItem">
                        <img src="../../../../assets/bigScreen/cockpit/t3d_unonline.svg" alt="" />
                        <div class="deviceInfoItem_text">
                            <div class="deviceInfoItem_text_title">离线设备</div>
                            <div class="c_wordColor deviceInfoItem_text_num">{{ deviceInfo.Offline }}</div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="weatherInfo">
                <div class="box_title">
                    <div class="c_wordColor word">气象监测</div>
                </div>
                <div class="weather_content">
                    <div class="weatherItem">
                        <div class="weatherItem_title">
                            <div class="desc">空气温度</div>
                        </div>
                        <div class="weatherItem_content">
                            <div class="centerbox">
                                <div class="img_div">
                                    <img src="../../../../assets/bigScreen/cockpit/t3d_wendu.png" alt="" />
                                </div>
                                <div class="c_wordColor num">{{ weatherInfo.temperature }}°C</div>
                            </div>
                        </div>
                    </div>
                    <div class="weatherItem">
                        <div class="weatherItem_title">
                            <div class="desc">空气湿度</div>
                        </div>
                        <div class="weatherItem_content">
                            <div class="centerbox">
                                <div class="img_div">
                                    <img src="../../../../assets/bigScreen/cockpit/t3d_shidu.png" alt="" />
                                </div>
                                <div class="c_wordColor num">{{ weatherInfo.humidity }}%</div>
                            </div>
                        </div>
                    </div>
                    <div class="weatherItem">
                        <div class="weatherItem_title">
                            <div class="desc">二氧化碳</div>
                        </div>
                        <div class="weatherItem_content">
                            <div class="centerbox">
                                <div class="img_div">
                                    <img src="../../../../assets/bigScreen/cockpit/t3d_co2.png" alt="" />
                                </div>
                                <div class="c_wordColor num">{{ weatherInfo.co2 }}</div>
                            </div>
                        </div>
                    </div>
                    <div class="weatherItem">
                        <div class="weatherItem_title">
                            <div class="desc">PM2.5</div>
                        </div>
                        <div class="weatherItem_content">
                            <div class="centerbox">
                                <div class="img_div">
                                    <img src="../../../../assets/bigScreen/cockpit/t3d_pm25.png" alt="" />
                                </div>
                                <div class="c_wordColor num">{{ weatherInfo.pm25 }}</div>
                            </div>
                        </div>
                    </div>
                    <div class="weatherItem">
                        <div class="weatherItem_title">
                            <div class="desc">光照强度</div>
                        </div>
                        <div class="weatherItem_content">
                            <div class="centerbox">
                                <div class="img_div">
                                    <img src="../../../../assets/bigScreen/cockpit/t3d_guangzhao.png" alt="" />
                                </div>
                                <div class="c_wordColor num">{{ weatherInfo.light }}</div>
                            </div>
                        </div>
                    </div>
                    <div class="weatherItem">
                        <div class="weatherItem_title">
                            <div class="desc">风速/风向</div>
                        </div>
                        <div class="weatherItem_content">
                            <div class="centerbox" style="width: 100%">
                                <div class="img_div" style="width: 50%; margin: 10px auto 0 auto">
                                    <img src="../../../../assets/bigScreen/cockpit/t3d_fengsu.png" alt="" />
                                </div>
                                <div class="c_wordColor num">{{ weatherInfo.wind_speed }}/{{ wind_direction_Desc }}</div>
                            </div>
                        </div>
                    </div>
                    <div class="weatherItem">
                        <div class="weatherItem_title">
                            <div class="desc">雨雪</div>
                        </div>
                        <div class="weatherItem_content">
                            <div class="centerbox">
                                <div class="img_div">
                                    <img src="../../../../assets/bigScreen/cockpit/t3d_yushui.png" alt="" />
                                </div>
                                <div class="c_wordColor num">{{ weatherInfo.snow == 1 ? '有' : '无' }}</div>
                            </div>
                        </div>
                    </div>
                    <div class="weatherItem">
                        <div class="weatherItem_title">
                            <div class="desc">气压</div>
                        </div>
                        <div class="weatherItem_content">
                            <div class="centerbox">
                                <div class="img_div">
                                    <img src="../../../../assets/bigScreen/cockpit/t3d_dashboard.png" alt="" />
                                </div>
                                <div class="c_wordColor num">{{ weatherInfo.pressure }}kPa</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="energyInfo">
                <div class="box_title">
                    <div class="c_wordColor word">能耗检控</div>
                </div>
                <div class="energy_content">
                    <div class="energy_leftItem">
                        <div class="energy_leftItem_title"></div>
                        <div class="energy_leftItem_box">
                            <div class="energy_leftItem_subtitle">区域一</div>
                            <div class="energy_leftItem_content">
                                <div class="desc">用电量（kWh）</div>
                                <div class="c_wordColor num">{{ powerInfo.electric1 }}</div>
                            </div>
                            <div class="energy_leftItem_content">
                                <div class="desc">电费（￥）</div>
                                <div class="c_wordColor num">{{ (powerInfo.electric1 * pingPowerPrice).toFixed(2) }}</div>
                            </div>
                            <div class="energy_leftItem_subtitle">区域二</div>
                            <div class="energy_leftItem_content">
                                <div class="desc">用电量（kWh）</div>
                                <div class="c_wordColor num">{{ powerInfo.electric2 }}</div>
                            </div>
                            <div class="energy_leftItem_content">
                                <div class="desc">电费（￥）</div>
                                <div class="c_wordColor num">{{ (powerInfo.electric2 * pingPowerPrice).toFixed(2) }}</div>
                            </div>
                            <div class="energy_leftItem_subtitle">区域三</div>
                            <div class="energy_leftItem_content">
                                <div class="desc">用电量（kWh）</div>
                                <div class="c_wordColor num">{{ powerInfo.electric3 }}</div>
                            </div>
                            <div class="energy_leftItem_content">
                                <div class="desc">电费（￥）</div>
                                <div class="c_wordColor num">{{ (powerInfo.electric3 * pingPowerPrice).toFixed(2) }}</div>
                            </div>
                        </div>
                    </div>
                    <div class="energy_rightItem">
                        <div class="energy_rightItem_top">
                            <div class="energy_rightItem_top_title"></div>
                            <div class="energy_leftItem_box">
                                <div class="energy_leftItem_content">
                                    <div class="desc">用水量（t）</div>
                                    <div class="c_wordColor num">{{ powerInfo.water }}</div>
                                </div>
                                <div class="energy_leftItem_content">
                                    <div class="desc">水费（￥）</div>
                                    <div class="c_wordColor num">{{ (powerInfo.water * waterPrice).toFixed(2) }}</div>
                                </div>
                            </div>
                        </div>
                        <div class="energy_rightItem_bottom">
                            <div class="energy_rightItem_bottom_title"></div>
                            <div class="energy_rightItem_bottom_centent">
                                <div class="dianchi"></div>
                                <div class="desc">储电量{{ powerInfo.power }}（Ah）</div>
                                <div class="desc2">续航时间（分钟）：{{ powerInfo.life_time }}</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { isNil } from 'lodash-es';
// import { getWindDict, getEnergyPrice } from '@/api/data/center';
export default {
    name: 'leftbox',
    components: {},
    props: {
        bigScreenData: {
            type: Object,
            default: () => {},
        },
    },
    data() {
        return {
            deviceInfo: {
                total: 20,
                alertNum: 20,
                online: 20,
                Offline: 20,
            },
            weatherInfo: {
                temperature: 36,
                humidity: 20,
                co2: 20,
                pm25: 20,
                light: 20,
                wind_direction: 4,
                wind_speed: 0,
                snow: 1,
                pressure: 7,
            },
            powerInfo: {
                electric1: 256,
                electric2: 256,
                electric3: 256,
                power: 256,
                water: 256,
                life_time: 2,
            },
            windDictData: [],
            waterPrice: 0,
            pingPowerPrice: 0,
        };
    },
    computed: {
        wind_direction_Desc() {
            let desc = '';
            const findItem = this.windDictData.find((i) => i.dictValue == this.weatherInfo.wind_direction);
            if (findItem) {
                desc = findItem.dictLabel;
            }
            return desc;
        },
    },
    watch: {
        bigScreenData: {
            handler(val) {
                this.bigScreenDatacChange(val);
            },
            deep: true,
        },
    },
    mounted() {
        this.getWindDictData();
        this.getEnergyPriceData();
    },
    beforeDestroy() {},
    methods: {
        isNilValue(val) {
            if (isNil(val)) {
                return '--';
            }
            return val;
        },
        bigScreenDatacChange(val) {
            this.deviceInfo.total = this.isNilValue(val.total);
            this.deviceInfo.alertNum = this.isNilValue(val.alertNum);
            this.deviceInfo.online = this.isNilValue(val.online);
            this.deviceInfo.Offline = this.isNilValue(val.Offline);

            this.weatherInfo.temperature = this.isNilValue(val.temperature);
            this.weatherInfo.humidity = this.isNilValue(val.humidity);
            this.weatherInfo.co2 = this.isNilValue(val.co2);
            this.weatherInfo.pm25 = this.isNilValue(val['pm2.5']);
            this.weatherInfo.light = this.isNilValue(val.light);
            this.weatherInfo.wind_direction = this.isNilValue(val.wind_direction);
            this.weatherInfo.wind_speed = this.isNilValue(val.wind_speed);
            this.weatherInfo.snow = this.isNilValue(val.snow);
            this.weatherInfo.pressure = this.isNilValue(val.pressure);

            this.powerInfo.electric1 = this.isNilValue(val.electric1);
            this.powerInfo.electric2 = this.isNilValue(val.electric2);
            this.powerInfo.electric3 = this.isNilValue(val.electric3);
            this.powerInfo.power = this.isNilValue(val.power);
            this.powerInfo.water = this.isNilValue(val.water);
            this.powerInfo.life_time = this.isNilValue(val.life_time);
        },
        getWindDictData() {
            getWindDict().then((res) => {
                this.windDictData = res.rows || [];
            });
        },
        getEnergyPriceData(){
            getEnergyPrice().then((res) => {
                const energyPriceList = res.data.list || [];
                const waterPriceObj = energyPriceList.find((i) => i.type == 3);
                const pingPowerPriceObj = energyPriceList.find((i) => i.type == 9);
                this.waterPrice = Number(waterPriceObj.price) || 0;
                this.pingPowerPrice = Number(pingPowerPriceObj.price) || 0;
            });
        }
    },
};
</script>
<style lang="scss" scoped>
.box {
    width: 100%;
    height: 100%;
    display: flex;
    gap: 10px;
    .leftImg {
        width: 30px;
        padding-top: 40px;
        .Img {
            height: 80%;
            width: 100%;
            background-image: url(../../../../assets/bigScreen/cockpit/FrameLeft.png);
            background-size: 100% 100%;
        }
    }
    .content {
        width: calc(100% - 30px);
        height: 100%;
        display: flex;
        flex-direction: column;
    }
}

.c_wordColor {
    font-family: Roboto;
    background-image: linear-gradient(to bottom, rgba(229, 232, 241, 1), rgba(255, 255, 255, 1), rgba(170, 207, 249, 1));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: transparent;
}

.box_title {
    width: 100%;
    height: 47px;
    background-image: url(../../../../assets/bigScreen/cockpit/t3d_title.png);
    background-size: 100% 100%;
    background-repeat: no-repeat;
    position: relative;
    .word {
        position: absolute;
        left: 12%;
