    display: flex;
    flex-direction: column;
    .deviceInfo_content {
        flex: 1;
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 14px;
        padding: 10px 20px 10px 10px;
        .deviceInfoItem {
            background: linear-gradient(180deg, rgba(1, 86, 223, 0) 0%, rgba(1, 86, 223, 0.3) 100%);
            border-radius: 10px;
            display: flex;
            > img {
                width: 40%;
                padding: 0 14px;
            }
            .deviceInfoItem_text {
                width: calc(100% - 45px);
                margin: auto;
                .deviceInfoItem_text_title {
                    color: rgba(161, 191, 255, 1);
                    font-size: 16px;
                    font-weight: 400;
                }
                .deviceInfoItem_text_num {
                    font-size: 20px;
                    font-weight: 700;
                    margin-top: 10px;
                }
            }
        }
    }
}
.weatherInfo {
    width: 100%;
    height: 34%;
    // border: 1px solid #863e3e;
    // box-sizing: content-box;
    display: flex;
    flex-direction: column;
    .weather_content {
        flex: 1;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
