                    margin: auto;
                    font-size: 16px;
                    font-weight: 400;
                    color: rgba(142, 171, 231, 1);
                }
            }
            .weatherItem_content {
                height: calc(100% - 34px);
                width: 100%;
                display: flex;
                justify-content: center;
                .centerbox {
                    width: 50%;
                    display: flex;
                    flex-direction: column;
                    .img_div {
                        border-bottom: 1px solid rgba(235, 249, 255, 0.3);
                        display: flex;
                        margin-top: 10px;
                        height: 40%;
                        > img {
                            margin: auto;
                        }
                    }
                    .num {
                        margin: auto;
                        font-size: 16px;
                        font-weight: 700;
                    }
                }
            }
        }
    }
}
.energy_leftItem_box {
    height: calc(100% - 27px);
    display: flex;
    flex-direction: column;
    gap: 4px;
    // overflow-y: auto;
    .energy_leftItem_subtitle {
        height: 28px;
        color: #ccd6ec;
        font-size: 12px;
        font-weight: 700;
        display: flex;
        align-items: center;
        padding: 0 10px;
    }
    .energy_leftItem_content {
        height: 28px;
        display: flex;
        background-color: rgba(7, 33, 65, 1);
        justify-content: space-between;
        align-items: center;
        padding: 0 10px;
        .desc {
            color: rgba(142, 171, 231, 1);
            font-size: 12px;
            font-weight: 400;
        }
        .num {
            font-size: 16px;
            font-weight: 500;
        }
    }
}
.energyInfo {
    width: 100%;
    height: 38%;
    // border: 1px solid #863e3e;
    // box-sizing: content-box;
    display: flex;
    flex-direction: column;
    .energy_content {
        flex: 1;
        display: flex;
        gap: 10px;
        padding-top: 10px;
        width: 95%;
        .energy_leftItem {
            height: 100%;
            width: 50%;
            display: flex;
            flex-direction: column;
            .energy_leftItem_title {
                height: 27px;
                background-image: url(../../../../assets/bigScreen/cockpit/t3d_yongdian.png);
                background-size: 100% 100%;
            }
        }
        .energy_rightItem {
            height: 100%;
            width: 50%;
            display: flex;
            flex-direction: column;
            gap: 10px;
            .energy_rightItem_top {
                // height: 30%;
                display: flex;
                flex-direction: column;
                .energy_rightItem_top_title {
                    height: 27px;
                    background-image: url(../../../../assets/bigScreen/cockpit/t3d_yongshui.png);
                    background-size: 100% 100%;
                }
            }
            .energy_rightItem_bottom {
                height: 70%;
                .energy_rightItem_bottom_title {
                    height: 27px;
                    background-image: url(../../../../assets/bigScreen/cockpit/t3d_xudianzhan.png);
                    background-size: 100% 100%;
                }
                .energy_rightItem_bottom_centent {
                    height: calc(100% - 27px);
                    display: flex;
                    flex-direction: column;
                    .dianchi {
                        width: 50%;
                        height: 65%;
                        margin: auto;
                        background-image: url(../../../../assets/bigScreen/cockpit/t3d_dianchi.svg);
                        background-size: 100% 100%;
                    }
                    .desc {
                        color: rgba(142, 171, 231, 1);
                        font-size: 12px;
                        font-weight: 400;
                        text-align: center;
                    }
                    .desc2 {
                        font-family: Roboto;
                        font-weight: 700;
                        font-size: 12px;
                        text-align: center;
                        background-image: linear-gradient(to bottom, #ffffff, #7af38e);
                        -webkit-background-clip: text;
                        -webkit-text-fill-color: transparent;
                        background-clip: text;
                        color: transparent;
                        margin-top: 10px;
                    }
                }
            }
        }
    }
}
</style>
