<template>
    <div class="cockpit">
        <CockpitHearBar></CockpitHearBar>
        <CockpitMain class="cockpitMain"></CockpitMain>
        <div class="footer"></div>
    </div>
</template>
<script>
import CockpitHearBar from './headerBar/index.vue';
import CockpitMain from './main/index.vue';
import autofit from 'autofit.js';
export default {
    name: 'bs',
    components: {
        CockpitHearBar,
        CockpitMain,
    },
    data() {
        return {};
    },
    computed: {},
    watch: {},
    mounted() {
        this.init();
    },
    beforeDestroy() {},
    methods: {
        init() {
            // 等比缩放设计图
            autofit.init({
                dw: 1920,
                dh: 1080,
                resize: true,
                el: '.cockpit', // 这里必须指定容器元素，不指定会默认绑到body，然后出现一些弹出层偏移的问题
            });            
        },
    },
};
</script>
<style lang="scss" scoped>
.cockpit {
    width: 100%;
    height: 100%;
    background-color: #081b30;
    // background-image:url(@/assets/cockpit/bg.png);
    // filter: hue-rotate(-10deg) saturate(1.2); // 调整色调
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .header {
        height: 100px;
    }
    .cockpitMain {
        flex: 1;
    }
    .footer {
        width: 100%;
        height: 2.5vh;
        background-image: url(../../../assets/bigScreen/cockpit/mainBottom.png);
        background-size: 100% 100%;
    }
}
</style>
