<template>
    <div class="deviceTablebox">
        <div class="box_title">
            <div class="c_wordColor word">设备上报</div>
        </div>
        <div class="box_content" ref="boxContent">
            <el-table :data="tableData" style="width: 100%" :max-height="tableHeight">
                <el-table-column prop="deviceName" label="设备名称" width="180"></el-table-column>
                <el-table-column prop="serialNumber" label="设备编号"></el-table-column>
                <el-table-column prop="modelName" label="物模型"></el-table-column>
                <el-table-column prop="logValue" label="上报值"></el-table-column>
                <el-table-column prop="strTime" label="上报时间" width="180"></el-table-column>
                <el-table-column prop="logType" label="类别"></el-table-column>
            </el-table>
        </div>
    </div>
</template>
<script>
export default {
    name: 'tbox',
    components: {},
    props: {
        bigScreenData: {
            type: Object,
            default: () => {},
        },
    },
    data() {
        return {
            tableHeight: 0,
            tableData: [],
        };
    },
    computed: {},
    watch: {
        bigScreenData: {
            handler(val) {
                this.bigScreenDatacChange(val);
            },
            deep: true,
        },
    },
    mounted() {
        this.updateTableHeight();
        window.addEventListener('resize', this.updateTableHeight);
    },
    beforeDestroy() {
        window.removeEventListener('resize', this.updateTableHeight);
    },
    methods: {
        updateTableHeight() {
            this.$nextTick(() => {
                if (this.$refs.boxContent) {
                    this.tableHeight = this.$refs.boxContent.clientHeight;
                }
            });
        },
        bigScreenDatacChange(val) {
            this.tableData = (val.largeScreenPullData || []).map((item) => {
                if (item.logType == 1) {
                    item.logType = '属性上报';
                }
                if (item.logType == 2) {
                    item.logType = '事件上报';
                }
                return item;
            });
        },
    },
};
</script>
<style lang="scss" scoped>
.c_wordColor {
    font-family: Roboto;
    background-image: linear-gradient(to bottom, rgba(229, 232, 241, 1), rgba(255, 255, 255, 1), rgba(170, 207, 249, 1));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: transparent;
}
.deviceTablebox {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}
.box_title {
    width: 50%;
    height: 47px;
    background-image: url(../../../../assets/bigScreen/cockpit/t3d_title.png);
    background-size: 100% 100%;
    background-repeat: no-repeat;
    position: relative;
    .word {
        position: absolute;
        left: 12%;
        font-size: 24px;
        font-weight: 700;
    }
}
.box_content {
    height: calc(100% - 57px);
}

::v-deep .el-table {
    background-color: transparent !important;

    .el-table__header-wrapper {
        background: linear-gradient(180deg, rgba(6, 95, 247, 0.0001) 38.67%, rgba(6, 95, 247, 0.76) 100%) !important;
    }

    tr {
        background-color: transparent !important;
    }

    thead th {
        background-color: transparent !important;
        color: rgba(170, 207, 249, 1) !important;
        font-size: 14px;
        font-weight: 700;
        border-bottom: none !important;
    }

    tbody tr {
        background-color: #0a244b !important;
    }

    tbody tr:nth-child(even) {
        background-color: #061a36 !important;
    }

    td {
        border-bottom: 1px solid #1c407e !important;
        color: rgba(254, 254, 254, 1) !important;
        font-size: 14px;
        font-weight: 400;
        padding: 8px 0 !important;
    }

    tr:hover > td {
        background-color: #0f3063 !important;
    }

    // 🔧 滚动条样式
    .el-table__body-wrapper {
        scrollbar-width: 6px;
        scrollbar-color: transparent;

        &::-webkit-scrollbar {
            width: 6px;
            height: 6px;
        }

        &::-webkit-scrollbar-track {
            background: rgba(6, 95, 247, 0.2);
            border-radius: 3px;
        }

        &::-webkit-scrollbar-thumb {
            background: rgba(37, 147, 253, 1);
            border-radius: 3px;
        }

        // &::-webkit-scrollbar-thumb:hover {
        //     background: linear-gradient(to bottom, #09558f, #0870ff);
        // }
    }
}
</style>
