<template>
    <div class="cockpitMain">
        <leftBox class="leftBox" :bigScreenData="bigScreenData"></leftBox>
        <centerBox class="centerBox" :bigScreenData="bigScreenData"></centerBox>
        <rightBox class="rightBox" :bigScreenData="bigScreenData"></rightBox>
    </div>
</template>
<script>
import leftBox from './leftbox.vue';
import centerBox from './centerbox.vue';
import rightBox from './rightbox.vue';
import {bigData} from './data.js';
// import { getlargeScreenData, getSerialDict } from '@/api/data/center';
export default {
    name: 'main',
    components: {
        leftBox,
        centerBox,
        rightBox,
    },
    data() {
        return {
            // 订阅url
            subscribeUrls: [],
            bigScreenData: {},
        };
    },
    computed: {},
    watch: {},
    mounted() {
        // this.connectMqtt();
        this.getSerialDictData();
    },
    beforeDestroy() {
        // 取消订阅主题
        this.$mqttTool.unsubscribe(this.subscribeUrls);
    },
    methods: {
        async connectMqtt() {
            if (this.$mqttTool.client == null) {
                await this.$mqttTool.connect(this.vuex_token);
            }
            // 删除所有message事件监听器
            this.$mqttTool.client.removeAllListeners('message');
            // 添加message事件监听器
            this.mqttCallback();
            this.getSerialDictData();
        },
        /* Mqtt回调处理  */
        mqttCallback() {
            this.$mqttTool.client.on('message', (topic, message, buffer) => {
                let topics = topic.split('/');
                let productId = topics[1];
                let deviceNum = topics[2];
                if (message instanceof Uint8Array) {
                    // 创建TextDecoder对象来转换Uint8Array到字符串
                    const decoder = new TextDecoder('utf-8');
                    const str = decoder.decode(message);
                    message = str; //转换后的字符串
                }
                if (!message) {
                    return;
                }
                try {
                    message = JSON.parse(message.toString());
                } catch (error) { }
                console.log('🚀 ~ this.$mqttTool.client.on ~ topics:', topic);
                console.log('🚀 ~ this.$mqttTool.client.on ~ message:', message);
                if (topics[3] == 'status' || topics[2] == 'status') {
                    console.log('接收到【设备状态-详情】主题:', topic);
                    console.log('接收到【设备状态-详情】内容：', message);
                    // 更新列表中设备的状态
                }
                if (topic.endsWith('ws/service')) {
                    this.updateBigScreenData(message.message, deviceNum);
                }
                if (topic.endsWith('largeScreenPullData')) {
                    this.bigScreenData.largeScreenPullData = (message.message || []).map((item) => {
                        item.logValue = item.logValue + (item.unit || '');
                        return item;
                    });
                }
                if (topic.endsWith('largeScreenAlertLog')) {
                    this.bigScreenData.largeScreenAlertLog = message.message || [];
                }
                if (topic.endsWith('largeScreenTotalData')) {
                    this.bigScreenData.total = message.message.total;
                    this.bigScreenData.alertNum = message.message.alertNum;
                    this.bigScreenData.online = message.message.online;
                    this.bigScreenData.Offline = message.message.Offline;
                }
            });
        },
        updateBigScreenData(mqttData, deviceNum) {
            console.log('updateBigScreenData@', mqttData, deviceNum);
            if (Array.isArray(mqttData)) {
                mqttData.forEach((mqttDataItem) => {
                    this.bigScreenData[mqttDataItem.id] = mqttDataItem.value;
                    if (mqttDataItem.id === 'electric') {
                        if (deviceNum == 'BCDDC2972620') {
                            this.bigScreenData.electric1 = mqttDataItem.value;
                        }
                        if (deviceNum == 'BCDDC2972630') {
                            this.bigScreenData.electric2 = mqttDataItem.value;
                        }
                        if (deviceNum == 'BCDDC2972640') {
                            this.bigScreenData.electric3 = mqttDataItem.value;
                        }
                    }
                });
            } else {
                console.warn('不标准的订阅数据', mqttData);
            }
        },
        async getSerialDictData() {
            // await getlargeScreenData().then((res) => {
            //     this.bigScreenData = res.data || {};
            //     this.bigScreenData.largeScreenPullData = (this.bigScreenData.largeScreenPullData || []).map((item) => {
            //         item.logValue = item.logValue + (item.unit || '');
            //         return item;
            //     });
            // });
            // getSerialDict().then((res) => {
            //     console.log('getSerialDict@', res);
            //     const deviceList = res.rows || [];
            //     this.subscribeTopicUrl(deviceList);
            // });

            this.bigScreenData = bigData;
        },
        subscribeTopicUrl(deviceList) {
            // 订阅当前设备状态和实时监测
            const topicService = '/ws/service';
            const topicStatus = '/status/post';
            const topics = [
                topicService,
                // topicStatus
            ];
            const urls = [];
            deviceList.forEach((deviceItem) => {
                topics.forEach((item) => {
                    let url = `/${deviceItem.dictValue}/${deviceItem.dictLabel}${item}`;
                    urls.push(url);
                });
            });
            urls.push('/iot/largeScreenAlertLog');
            urls.push('/iot/largeScreenPullData');
            urls.push('/iot/largeScreenTotalData');
            this.subscribeUrls = urls;
            this.$mqttTool.subscribe(this.subscribeUrls);
        },
    },
};
</script>
<style lang="scss" scoped>
.cockpitMain {
    display: flex;
    justify-content: space-between;
    overflow: hidden;

    .leftBox {
        width: 25%;
    }

    .centerBox {
        width: 50%;
    }

    .rightBox {
        width: 25%;
    }
}
</style>
