<template>
    <div class="radio-group">
        <label v-for="option in options" :key="option.value">
            <input type="radio" :value="option.value" :checked="value === option.value" :name="name" @change="handleChange" />
            <span class="radio-label">{{ option.label }}</span>
        </label>
    </div>
</template>

<script>
export default {
    props: {
        model: {
            prop: 'value',
            event: 'update:value',
        },
        value: {
            type: String,
            required: true,
        },
        options: {
            type: Array,
            default: () => [],
        },
        name: {
            type: String,
            default: 'radio-group',
        },
    },
    methods: {
        handleChange(event) {
            this.$emit('update:value', event.target.value);
        },
    },
};
</script>

<style scoped>
/* 上述的 Radio CSS 样式 */
.radio-group {
    display: flex;
}

.radio-group label {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.radio-group input[type='radio'] {
    appearance: none;
    width: 0;
    height: 0;
    margin: 0;
}

.radio-group .radio-label {
    padding: 4px 12px;
    transition: background-color 0.3s, color 0.3s;
    font-family: Roboto;
    font-weight: 400;
    font-size: 16px;
}

/* 第一个 label：左上、左下圆角 */
.radio-group label:first-child .radio-label {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

/* 最后一个 label：右上、右下圆角 */
.radio-group label:last-child .radio-label {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}

/* 中间项默认无圆角 */
.radio-group label:not(:first-child):not(:last-child) .radio-label {
    border-radius: 0;
}

.radio-group input[type='radio']:checked + .radio-label {
    background-color: rgba(37, 147, 253, 1);
    color: rgba(255, 255, 255, 1);
}

.radio-group input[type='radio']:not(:checked) + .radio-label {
    background-color: rgba(37, 147, 253, 0.2);
    color: rgba(255, 255, 255, 1);
}
</style>
