<template>
    <label class="cswitch">
        <input type="checkbox" :checked="modelValue" @change="handleChange" />
        <span class="slider"></span>
    </label>
</template>

<script>
export default {
    model: {
        prop: 'modelValue',
        event: 'update:modelValue',
    },
    props: {
        modelValue: {
            type: Boolean,
            default: false,
        },
    },
    methods: {
        handleChange(event) {
            this.$emit('update:modelValue', event.target.checked);
        },
    },
};
</script>

<style lang="scss" scoped>
/* 上述的 Switch CSS 样式 */
.cswitch {
    position: relative;
    display: inline-block;
    width: 44px;
    height: 22px;
}

.cswitch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(37, 147, 253, 0.1);
    transition: 0.4s;
    border-radius: 34px;
}

.slider:before {
    position: absolute;
    content: '';
    height: 18px;
    width: 18px;
    left: 4px;
    bottom: 2px;
    background-color: white;
    transition: 0.4s;
    border-radius: 50%;
}

input:checked + .slider {
    background-color: #2196f3;
}

input:focus + .slider {
    box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
    transform: translateX(18px);
}
</style>
