<template>
    <div class="header">
        <div class="headerTop"></div>
        <div class="Container"></div>
        <div class="headerContainer">
            <div class="left">
                <img src="../../../../assets/bigScreen/cockpit/Local.svg" alt />
                <div class="city">
                    {{ city }}
                </div>
                <!-- <img :src="`@/assets/cockpit/weather/${icon}.svg`" alt /> -->
                <img :src="icon" alt />
                <div class="weather">{{ weather }}</div>
                <div class="nightTemperature">{{ nightTemperature }}</div>
                ~
                <div class="temperature">{{ temperature }}</div>
            </div>
            <div class="center_title">
                {{ centerTitle }}
            </div>
            <div class="right">
                <div class="timeBox">
                    <div class="time">{{ time }}</div>
                    <div class="line"></div>
                    <div class="dateBox">
                        <div class="week">{{ week }}</div>
                        <div class="date">{{ date }}</div>
                    </div>
                </div>
                <div class="avatar-container">
                    <el-dropdown class="right-menu-item hover-effect" trigger="click" @command="handleCommand">
                        <div class="avatar-wrapper">
                            <img src="../../../../assets/bigScreen/cockpit/avatar.png" class="user-avatar" />
                            <i class="el-icon-caret-bottom"></i>
                        </div>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="sys">后台系统</el-dropdown-item>
                            <el-dropdown-item command="loginout">退出登录</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'header',
    components: {},
    data() {
        return {
            icon: '',
            city: '',
            time: '',
            weather: '',
            nightTemperature: '',
            temperature: '',
            centerTitle: '智慧园区管理平台',
            time: '',
            week: '',
            date: '',
            apiKey: '8fdc563df40c912c791c20257ec2c885',
            weatherKey: '142662304155f764c457f0ac9410b5e2',
            weatherTranslations: [
                { label: ['晴'], value: 'Sunny' },
                { label: ['多云', '晴间多云', '少云', '阴'], value: 'Cloudy' },
                { label: ['阴'], value: 'Overcast' },
                { label: ['小雨', '毛毛雨/细雨', '雨', '小雨-中雨'], value: 'LightRain' },
                { label: ['阵雨', '强阵雨'], value: 'shower' },
                { label: ['中雨', '中雨-大雨'], value: 'ModerateRain' },
                { label: ['大雨'], value: 'HeavyRain' },
                { label: ['暴雨', '特大暴雨', '极端降雨', '大暴雨', '大雨-暴雨', '暴雨-大暴雨', '大暴雨-特大暴雨'], value: 'TorrentialRain' },
                { label: ['雷阵雨', '雷阵雨并伴有冰雹', '强雷阵雨'], value: 'Thunderstorm' },
                { label: ['雨夹雪', '雨雪天气', '阵雨夹雪', '冻雨'], value: 'sleet' },
                { label: ['小雪', '雪', '阵雪', '小雪-中雪'], value: 'LightSnow' },
                { label: ['中雪', '中雪-大雪'], value: 'ModerateSnow' },
                { label: ['大雪', '暴雪', '大雪-暴雪'], value: 'HeavySnow' },
                { label: ['暴雪'], value: 'Blizzard' },
                { label: ['雾', '浓雾', '强浓雾', '特强浓雾', '大雾', '轻雾'], value: 'Fog' },
                { label: ['霾', '中度霾', '重度霾', '严重霾'], value: 'Haze' },
                { label: ['沙尘暴', '扬沙', '浮尘', '强沙尘暴'], value: 'Sandstorm' },
                { label: ['微风', '和风', '清风', '强风', '疾风', '大风', '烈风', '狂风', '飓风', '龙卷风', '热带风暴', '有风', '龙卷风'], value: 'Wind' },
            ],
            timer: null,
        };
    },
    computed: {},
    watch: {},
    mounted() {
        this.getAddress();
        this.getDateTime();
        this.timer = setInterval(() => {
            this.onlyGetTime();
        }, 1000);
    },
    beforeDestroy() {
        clearInterval(this.timer);
    },
    methods: {
        dateFormatter(date, options) {
            options = options ? options : {};
            options.datelabel = options.datelabel ? options.datelabel : '-'; //日期分隔符
            options.timelabel = options.timelabel ? options.timelabel : ':'; //时间分隔符
            options.separator = options.separator ? options.separator : ' '; //日期时间分隔符
            options.hasTime = options.hasTime ? options.hasTime : false; //返回值是否带时间
            options.hasWeek = options.hasWeek ? options.hasWeek : false; //返回值是否带星期
            options.hasTimeUnit = options.hasTimeUnit ? options.hasTimeUnit : false; //是否使用时间单位
            options.hasDateUnit = options.hasDateUnit ? options.hasDateUnit : false; //是否使用日期单位
            //是否返回格式化后的数组 若为true,则返回值为一个对象,formatter为自定义格式,array为格式化后的字符串信息
            options.hasArray = options.hasArray ? options.hasArray : false;
            let d = new Date(date);
            let year = d.getFullYear();
            let month = d.getMonth() + 1 < 10 ? '0' + (d.getMonth() + 1) : d.getMonth() + 1;
            let day = d.getDate() < 10 ? '0' + d.getDate() : d.getDate();
            let hours = d.getHours() < 10 ? '0' + d.getHours() : d.getHours();
            let minute = d.getMinutes() < 10 ? '0' + d.getMinutes() : d.getMinutes();
            let second = d.getSeconds() < 10 ? '0' + d.getSeconds() : d.getSeconds();
            let formatterArray = [];
            let formatter = '';
            if (options.hasDateUnit == true) {
                formatter = year + '年' + month + '月' + day + '日';
            } else {
                formatter = year + options.datelabel + month + options.datelabel + day;
            }
            formatterArray.push(formatter);
            if (options.hasTime == true && options.hasTimeUnit == false) {
                formatter += options.separator + hours + options.timelabel + minute + options.timelabel + second;
                formatterArray.push(hours + options.timelabel + minute + options.timelabel + second);
            } else if (options.hasTime == true && options.hasTimeUnit == true) {
                formatter += options.separator + hours + '时' + minute + '分' + second + '秒';
                formatterArray.push(hours + '时' + minute + '分' + second + '秒');
            } else if (options.hasTime == false && options.hasTimeUnit == false) {
                formatterArray.push(hours + options.timelabel + minute + options.timelabel + second);
            } else if (options.hasTime == false && options.hasTimeUnit == true) {
                formatterArray.push(hours + '时' + minute + '分' + second + '秒');
            }
            let weekList = ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六'];
            if (options.hasWeek == true) {
                formatter += ' ' + weekList[d.getDay()];
                formatterArray.push(weekList[d.getDay()]);
            } else {
                formatterArray.push(weekList[d.getDay()]);
            }
            if (options.hasArray == true) {
                return {
                    formatter: formatter,
                    array: formatterArray,
                };
            }
            return formatter;
        },
        getAddress() {
            fetch(`https://restapi.amap.com/v3/ip?key=${this.apiKey}`)
                .then((response) => response.json())
                .then((data) => {
                    // 在这里处理返回的数据
                    this.city = data.city;
                    this.adcode = data.adcode;
                    this.getWeather(data.adcode);
                })
                .catch((error) => {
                    console.error('获取地理位置信息时发生错误:', error);
                });
        },
        getWeather(city) {
            fetch(`https://restapi.amap.com/v3/weather/weatherInfo?city=${city}&key=${this.weatherKey}&extensions=all`)
                .then((response) => response.json())
                .then((data) => {
                    // 在这里处理返回的数据
                    this.setWeather(data.forecasts[0]);
                })
                .catch((error) => {
                    console.error('获取天气信息时发生错误:', error);
                });
        },
        setWeather(data) {
            this.city = data.city;
            const dayWeather = data.casts[0];
            this.weather = dayWeather.dayweather;
            this.temperature = dayWeather.daytemp + '℃';
            this.nightTemperature = dayWeather.nighttemp + '℃';
            const _index = this.weatherTranslations.findIndex((item) => item.label.includes(dayWeather.dayweather));
            const iconValue = this.weatherTranslations[_index].value;
            this.icon = this.getWeatherImgByName(`${iconValue}.svg`);
        },
        getWeatherImgByName(name) {
            return require('../../../../assets/bigScreen/cockpit/weather/' + name);
        },
        getDateTime() {
            let d = this.dateFormatter(new Date(), { hasArray: true });
            this.time = d.array[1];
            this.date = d.array[0];
            this.week = d.array[2];
        },
        onlyGetTime() {
            let d = this.dateFormatter(new Date(), { hasArray: true });
            this.time = d.array[1];
        },
        handleCommand(command) {
            switch (command) {
                case 'loginout':
                    this.handleLogout();
                    break;
                case 'sys':
                    this.$router.back();
                    break;
                default:
                    break;
            }
        },
        handleLogout() {
            this.$confirm(this.$t('login.989807-31'), this.$t('login.989807-32'), {
                confirmButtonText: this.$t('confirm'),
                cancelButtonText: this.$t('cancel'),
                type: 'warning',
            })
                .then(() => {
                    this.$store.dispatch('LogOut').then(() => {
                        location.href = '/index';
                    });
                })
                .catch(() => {});
        },
    },
};
</script>
<style lang="scss" scoped>
.header {
    position: relative;
    .headerTop {
        height: 50px;
        background-image: url(../../../../assets/bigScreen/cockpit/headerTop.png);
        background-size: 100% 100%;
    }
    .Container {
        position: absolute;
        top: 0;
        height: 106px;
        width: 59.06%;
        left: 50%;
        transform: translate(-50%, 0);
        background-image: url(../../../../assets/bigScreen/cockpit/headerContainer.svg);
        background-size: 100% 100%;
    }
    .headerContainer {
        position: absolute;
        // bottom: 1.92vh;
        bottom: 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        // height: 5.4vh;
        height: 52px;
        width: 100%;
        color: #d5f1ff;
        .left {
            display: flex;
            align-items: center;
            padding-left: 1.3vw;
            color: #d5f1ff;
            .city {
                margin-left: 0.5vw;
                margin-right: 1vw;
            }
            .weather {
                margin-right: 0.5vw;
            }
            .nightTemperature {
                margin-right: 0.3vw;
            }
            .temperature {
                margin-left: 0.3vw;
            }
        }
        .right {
            display: flex;
            .timeBox {
                display: flex;
                align-items: center;
                justify-content: center;
                .line {
                    height: 1.8vh;
                    border: 1px solid #ffffff33;
                }
                .time {
                    margin-right: 0.8vw;
                    font-family: DigitalNumbers;
                    font-size: 18px;
                    font-weight: 400;
                    // line-height: 16.38px;
                    text-align: left;
                }
                .dateBox {
                    margin: 0 0.8vw;
                    font-family: FZZYJW;
                    font-size: 12px;
                    font-weight: 400;
                    text-align: left;
                    .week {
                        margin-bottom: 0.4vh;
                    }
                }
            }
        }
        .center_title {
            font-size: 32px;
            background: linear-gradient(to bottom, #e5e8f1 23.3%, #02c7fd 44.6%, #12a4ff 71.93%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            font-weight: 700;
            text-shadow: 0px 3px 4px rgba(0, 0, 0, 0.3);
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
        }
    }
}
.avatar-container {
    // position: absolute;
    // bottom: 15px;
    // right: 0;
    margin-right: 40px;

    .avatar-wrapper {
        margin-top: 5px;
        position: relative;

        .user-avatar {
            cursor: pointer;
            width: 40px;
            height: 40px;
            border-radius: 10px;
        }

        i {
            cursor: pointer;
            position: absolute;
            right: -26px;
            top: 12px;
            font-size: 20px;
            /* 添加渐变色 */
            background-image: linear-gradient(to right, rgba(213, 241, 255, 1), rgba(2, 190, 253, 1), rgba(2, 82, 250, 1), rgba(2, 82, 250, 1));
            -webkit-background-clip: text; /* 文字渐变 */
            -webkit-text-fill-color: transparent; /* 避免文字颜色覆盖渐变 */
            display: inline-block; /* 支持背景 */
        }
    }
}
</style>
