<template>
    <div class="box">
        <!-- Three.js 渲染容器 -->
        <div class="canvas" ref="container" v-show="monitorContent == '视频'">
            <img src="../../../../assets/bigScreen/cockpit/threebg.png" alt="" srcset="">
        </div>
        <div class="canvas" v-show="monitorContent == '模型'">
            <video id="rtcVideo" controls autoplay autobuffer muted preload="auto"></video>
        </div>

        <!-- 进度条组件 -->
        <div v-if="showLoadingBar && monitorContent == '视频'" class="loading-bar">
            <div class="progress" :style="{ width: loadingProgress + '%' }"></div>
            <div class="percentage">{{ loadingText }} {{ loadingProgress.toFixed(0) }}%</div>
        </div>
    </div>
</template>
<script>
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { throttle } from 'lodash-es';
export default {
    name: 't3d',
    components: {},
    data() {
        return {
            scene: null,
            camera: null,
            renderer: null,
            controls: null,
            rafId: null,
            monitorContent: '视频',
            showLoadingBar: false, // 控制是否显示进度条
            loadingProgress: 0, // 当前加载进度百分比
            loadingText: '',
        };
    },
    computed: {},
    watch: {},
    mounted() {
        // 使用节流优化 resize 事件
        window.addEventListener('resize', throttle(this.onWindowResize, 20));
        // 监听custom-event
        document.addEventListener('changeMonitor', (event) => {
            this.handleChangeMonitor(event);
        });
        setTimeout(() => {
            // this.initThree();
            // this.loadGlbModel();
            // this.animate();
        });
    },
    beforeDestroy() {
        window.removeEventListener('resize', this.onWindowResize);
        if (this.rafId) {
            cancelAnimationFrame(this.rafId);
        }
        if (this.renderer) {
            this.renderer.dispose();
            this.renderer.forceContextLoss();
            this.renderer = null;
        }
        if (this.scene) {
            this.scene.clear();
            this.scene = null;
        }
        document.removeEventListener('changeMonitor', this.handleChangeMonitor);
    },
    methods: {
        handleChangeMonitor(event) {
            // 获取自定义事件中的数据
            const { monitorContent } = event.detail;
            this.monitorContent = monitorContent;
            if (this.monitorContent === '模型') {
                this.takeScreenshot();
            }
        },
        initThree() {
            const container = this.$refs.container;

            // 创建场景
            this.scene = new THREE.Scene();
            this.scene.background = null; // 设置为 null 表示透明背景

            // 相机设置
            const width = container.clientWidth;
            const height = container.clientHeight;
            this.camera = new THREE.PerspectiveCamera(45, width / height, 0.1, 1000);
            this.camera.position.set(0, 2, 5);

            // 渲染器
            // 渲染器（设置 alpha 通道为 true）
            this.renderer = new THREE.WebGLRenderer({
                antialias: true,
                alpha: true, // 允许透明背景
                logarithmicDepthBuffer: true, // 提升深度缓冲精度
            });
            this.renderer.setSize(width, height);
            this.renderer.setPixelRatio(window.devicePixelRatio);
            // this.renderer.domElement.style.backgroundColor = 'transparent'; // 确保 DOM 背景透明
            container.appendChild(this.renderer.domElement);

            // 控制器（鼠标操作）
            this.controls = new OrbitControls(this.camera, this.renderer.domElement);
            // this.controls.enableDamping = true; // 启用惯性
        },
        loadFBXModel() {
            const loader = new FBXLoader();
            loader.load(
                // '/three3d/园区.fbx',
                '/three3d/奶龙.fbx',
                (object) => {
                    this.model = object;

                    // 加载贴图
                    const texture = new THREE.TextureLoader().load('/three3d/texture_pbr_v128.png');

                    // 替换材质以响应光照
                    this.model.traverse((child) => {
                        if (child.isMesh) {
                            child.material = new THREE.MeshStandardMaterial({
                                // color: child.material.color,
                                // map: child.material.map,
                                map: texture,
                                metalness: 0.3,
                                roughness: 0.7,
                            });
                        }
                    });

                    // 居中模型
                    const box = new THREE.Box3().setFromObject(this.model);
                    const center = new THREE.Vector3();
                    box.getCenter(center);

                    // 将模型移动到原点
                    this.model.position.sub(center); // 减去中心点坐标，使中心对齐原点
                    this.scene.add(this.model);

                    // 设置光源
                    // 环境光（整体亮度）
                    const ambientLight = new THREE.AmbientLight(0xffffff, 0.5);
                    this.scene.add(ambientLight);
                    // 平行光（主方向光）
                    const directionalLight = new THREE.DirectionalLight(0xffffff, 0.5);
                    directionalLight.position.set(0, 10, 10);
                    directionalLight.castShadow = true;
                    this.scene.add(directionalLight);
                    // 半球光（补充顶部/底部光照）
                    const hemiLight = new THREE.HemisphereLight(0xffffff, 0x444444, 0.6);
                    this.scene.add(hemiLight);

                    // 自动调整相机视角到模型中心
                    const boxSize = box.getSize(new THREE.Vector3());
                    const maxDim = Math.max(boxSize.x, boxSize.y, boxSize.z);
                    const fov = this.camera.fov * (Math.PI / 180);
                    let cameraZ = maxDim / 2 / Math.tan(fov / 2);
                    cameraZ *= 1.2; // 加一点边距

                    this.camera.position.set(0, 0, cameraZ);
                    this.camera.lookAt(0, 0, 0);
                },
                undefined,
                (error) => {
                    console.error('加载FBX模型失败:', error);
                }
            );
        },
        loadGlbModel() {
            const loadingManager = new THREE.LoadingManager();

            // 加载开始
            loadingManager.onStart = (url, itemsLoaded, itemsTotal) => {
                this.loadingText = '正在加载模型文件...';
                this.loadingProgress = 0;
                this.showLoadingBar = true;
            };

            // 加载中
            loadingManager.onProgress = (url, itemsLoaded, itemsTotal) => {
                const progress = (itemsLoaded / itemsTotal) * 100;

                if (progress < 30) {
                    this.loadingText = '正在加载模型文件...';
                } else if (progress < 70) {
                    this.loadingText = '正在解析模型数据...';
                } else {
                    this.loadingText = '正在加载贴图资源...';
                }
                this.loadingProgress = progress;

                console.log(`加载进度: ${progress.toFixed(2)}%`);
            };

            // 加载完成
            loadingManager.onLoad = () => {
                // 可选：隐藏进度条或执行其他操作
                this.showLoadingBar = false;
            };

            // 加载错误
            loadingManager.onError = (url) => {
                console.error('加载失败:', url);
            };

            const loader = new GLTFLoader(loadingManager);

            loader.load(
                './three3d/园区.glb', // 替换为你自己的 .glb 文件路径
                (gltf) => {
                    this.model = gltf.scene;

                    // 遍历模型中的所有网格并替换材质
                    // this.model.traverse((child) => {
                    //     if (child.isMesh) {
                    //         child.material = new THREE.MeshStandardMaterial({
                    //             color: child.material.color,
                    //             map: child.material.map,
                    //             metalness: 0.3,
                    //             roughness: 0.7,
                    //         });
                    //     }
                    // });

                    // 居中模型
                    const box = new THREE.Box3().setFromObject(this.model);
                    const center = new THREE.Vector3();
                    box.getCenter(center);
                    this.model.position.sub(center); // 移动到原点

                    // 设置缩放（例如放大 1.8 倍）
                    this.model.scale.set(1.8, 1.8, 1.8);

                    // 添加到场景
                    this.scene.add(this.model);

                    // 设置光源
                    const ambientLight = new THREE.AmbientLight(0xffffff, 1.2);
                    this.scene.add(ambientLight);

                    const directionalLight = new THREE.DirectionalLight(0xffffff, 2.0);
                    directionalLight.position.set(10, 10, 10);
                    directionalLight.castShadow = true;
                    directionalLight.shadow.mapSize.width = 2048;
                    directionalLight.shadow.mapSize.height = 2048;
                    this.scene.add(directionalLight);

                    // 自动调整相机视角到模型中心
                    const boxSize = box.getSize(new THREE.Vector3());
                    const maxDim = Math.max(boxSize.x, boxSize.y, boxSize.z);
                    const fov = this.camera.fov * (Math.PI / 180);
                    let cameraZ = maxDim / 2 / Math.tan(fov / 2);
                    cameraZ *= 1.2; // 加一点边距

                    this.camera.position.set(0, cameraZ * 0.8, cameraZ);
                    this.camera.lookAt(0, 0, 0);

                    // 可选：更新控制器目标
                    this.controls.target.set(0, 0, 0);
                    this.controls.update();
                },
                undefined,
                (error) => {
                    console.error('加载GLB模型失败:', error);
                }
            );
        },
        onWindowResize() {
            const container = this.$refs.container;

            if (!container) return;

            const width = container.clientWidth;
            const height = container.clientHeight;

            this.camera.aspect = width / height;
            this.camera.updateProjectionMatrix();

            this.renderer.setSize(width, height);
        },
        animate() {
            if (!this.renderer) return;
            this.rafId = requestAnimationFrame(this.animate);
            this.renderer.render(this.scene, this.camera);
            this.controls.update();
        },
        takeScreenshot() {
            if (!this.renderer || !this.renderer.domElement) {
                console.error('Renderer or domElement is not available.');
                return;
            }

            // 确保最后一帧渲染完成
            requestAnimationFrame(() => {
                const canvas = this.renderer.domElement;

                // 防止 canvas 宽高为 0
                if (canvas.width === 0 || canvas.height === 0) {
                    console.warn('Canvas 尺寸为 0，无法截图');
                    return;
                }

                // 下载截图
                const dataURL = canvas.toDataURL('image/png', 0.5);
                // const link = document.createElement('a');
                // link.href = dataURL;
                // link.download = 'threejs-screenshot.png';
                // link.click();
                // link.remove();

                // 发射custom自定义事件
                let custom = new CustomEvent('takeScreenshot', {
                    detail: {
                        dataURL: dataURL,
                    },
                });
                document.dispatchEvent(custom);
            });
        },
    },
};
</script>
<style lang="scss" scoped>
.box {
    width: 100%;
    height: 100%;
    background-image: url(../../../../assets/bigScreen/cockpit/t3d_3dborder.png);
    background-size: 100% 100%;
    background-repeat: no-repeat;
    position: relative;
    display: flex;
    .canvas {
        width: calc(100% - 20px);
        height: calc(100% - 20px);
        margin: auto;
        display: flex;
        // z-index: 1;
        > video {
            width: 100%;
            height: 100%;
            object-fit: fill;
        }

        >img{
            width: 100%;
            height: 70%;
            margin: auto;
        }
    }
}
.loading-bar {
    position: absolute;
    top: 50%; // 距离顶部位置
    left: 50%; // 水平居中
    transform: translate(-50%, -50%); // 精准居中
    width: 300px;
    height: 20px;
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 10px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10;
    font-family: Arial, sans-serif;

    .progress {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        background-color: #2196f3;
        transition: width 0.2s ease-out;
        border-radius: 10px 0 0 10px;
        width: 0%; /* 初始宽度为0 */
        pointer-events: none;
    }

    .percentage {
        position: relative;
        z-index: 1;
        color: white;
        font-size: 14px;
        padding-left: 10px;
        pointer-events: none;
    }
}
</style>
