                </div>
                <div class="monitor_content" v-show="monitorContent == '视频'">
                    <video id="rtcVideo2" autoplay autobuffer muted preload="auto"></video>
                </div>
                <div class="monitor_content" v-show="monitorContent == '模型'">
                    <!-- <img :src="threeScreenshot" alt="预览" v-if="threeScreenshot" /> -->
                     <img src="../../../../assets/bigScreen/cockpit/threebg.png" alt="" srcset="">
                </div>
