                </div>
                <div class="waring_content">
                    <div class="content_item" v-for="item in waringList">
                        <div class="title">
                            <span>{{ item.deviceName }}</span>
                            <span>{{ item.statusDesc }}</span>
                        </div>
                        <div class="item_attrs">
                            <span class="attrs_title">告警：</span>
                            <span class="attrs_content">{{ item.alertName }}</span>
                        </div>
                        <div class="item_attrs">
                            <span class="attrs_title">级别：</span>
                            <span class="attrs_content" :style="{ color: alertLevelDescColor(item.alertLevel) }">{{ item.alertLevelDesc }}</span>
                        </div>
                        <div class="item_attrs">
                            <span class="attrs_title">时间：</span>
                            <span class="attrs_content">{{ item.strTime || item.createTime }}</span>
                        </div>
                        <!-- <div class="item_attrs">
                            <span class="attrs_title">原因：</span>
                            <span class="attrs_content" :title="item.remark">{{ item.remark }}</span>
                        </div> -->
                    </div>
                </div>
            </div>
            <div class="device">
                <div class="box_title">
                    <div class="c_wordColor word">设备管理</div>
                </div>
                <div class="device_content">
                    <div class="device_row" v-for="(item, index) in deviceMList" :key="index">
                        <div class="device_left" v-if="item.left">
                            <div class="title">{{ item.left.name }}</div>
                            <div class="radioBtn">
                                <CRadio v-if="item.left.isRadio" v-model="item.left.radioValue" :options="item.left.options" :name="'radio-group' + index" @update:value="radioChange($event, item.left)"></CRadio>
                            </div>
                            <div class="switchBtn">
                                <CSwitch v-model="item.left.switchValue" @update:modelValue="switchChange($event, item.left)" />
                            </div>
                        </div>
                        <div class="device_right" v-if="item.right">
                            <div class="title">{{ item.right.name }}</div>
                            <div class="switchBtn">
                                <CSwitch v-model="item.right.switchValue" @update:modelValue="switchChange($event, item.right)" />
                            </div>
                        </div>
                        <div v-if="item.center" class="device_center">
                            <div class="title">{{ item.center.name }}</div>
                            <el-slider style="width: calc(100% - 100px)" v-model="item.center.value" :step="1" :max="3" :marks="item.center.marks" :show-tooltip="false" @change="sliderChange"></el-slider>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="rightImg">
            <div class="Img"></div>
        </div>
    </div>
</template>
<script>
import CSwitch from './cswitch.vue';
import CRadio from './cradio.vue';
// import { setLargeScreenData, setCloseAll } from '@/api/data/center';
export default {
    name: 'rightbox',
    components: {
        CSwitch,
        CRadio,
    },
    props: {
        bigScreenData: {
            type: Object,
            default: () => {},
        },
    },
    data() {
        const lightOptions = [
            { value: '白', label: '白' },
            { value: '暖白', label: '暖白' },
        ];
        return {
            waringList: [],
            deviceMList: [
                {
                    left: {
                        name: '建筑灯左',
                        radioProp: '6',
                        radioValue: '白',
                        options: lightOptions,
                        isRadio: true,
                        switchProp: '6',
                        switchValue: false,
                    },
                    right: {
                        name: '路灯',
                        radioProp: '',
                        radioValue: '',
                        options: [],
                        isRadio: false,
                        switchProp: '9',
                        switchValue: false,
                    },
                },
                {
                    left: {
                        name: '建筑灯右',
                        radioProp: '7',
                        radioValue: '白',
                        options: lightOptions,
                        isRadio: true,
                        switchProp: '7',
                        switchValue: false,
                    },
                    right: {
                        name: '喷泉',
                        radioProp: '',
                        radioValue: '',
                        options: [],
                        isRadio: false,
                        switchProp: '2',
                        switchValue: false,
                    },
                },
                {
                    left: {
                        name: '安防演练',
                        radioProp: '',
                        radioValue: '',
                        options: [],
                        isRadio: false,
                        switchProp: '3',
                        switchValue: false,
                    },
                    right: {
                        name: '雨滴',
                        radioProp: '',
                        radioValue: '',
                        options: [],
                        isRadio: false,
                        switchProp: '10',
                        switchValue: false,
                    },
                },
                {
                    left: {
                        name: '消防演习',
                        radioProp: '',
                        radioValue: '',
                        options: [],
                        isRadio: false,
                        switchProp: '1',
                        switchValue: false,
                    },
                    right: {
                        name: '风电发电',
                        radioProp: '',
                        radioValue: '',
                        options: [],
                        isRadio: false,
                        switchProp: '4',
                        switchValue: false,
                    },
                },
                {
                    left: {
                        name: '巡检',
                        radioProp: '',
                        radioValue: '',
                        options: [{ label: '路径', value: '路径' }],
                        isRadio: true,
                        switchProp: '20',
                        switchValue: false,
                    },
                    right: {
                        name: '备用电源',
                        radioProp: '',
                        radioValue: '',
                        options: [],
                        isRadio: false,
                        switchProp: '5',
                        switchValue: false,
                    },
                },
                {
                    center: {
                        name: '风速',
                        isSlider: true,
                        prop: 'wind_speed',
                        marks: {
                            0: '关',
                            1: '1',
                            2: '2',
                            3: '3',
                        },
                        value: 0,
                    },
                },
            ],
            webRtcServer: null,
            webRtcServer2: null,
            monitorContent: '视频',
            threeScreenshot: '',
        };
    },
    computed: {},
    watch: {
        bigScreenData: {
            handler(val) {
                this.bigScreenDatacChange(val);
            },
            deep: true,
        },
    },
    mounted() {
        // this.initRTC();
        // 监听custom-event
        document.addEventListener('takeScreenshot', (event) => {
            this.threeScreenshot = event.detail.dataURL;
        });
        window.addEventListener('beforeunload', this.handleBeforeUnload);
    },
    beforeDestroy() {
        this.webRtcServer && this.webRtcServer.disconnect();
        this.webRtcServer2 && this.webRtcServer2.disconnect();
        window.removeEventListener('beforeunload', this.handleBeforeUnload);
    },
    methods: {
        getWaringPage() {
            this.$router.push('/iotdev/ruleengine/alert');
        },
        switchChange(value, item) {
            this.sendLargeScreenData({
                data: item,
                isLight: false,
            });
        },
        radioChange(value, item) {
            item.radioValue = value;
            if (value == '路径') {
                this.$router.push('/device-maintain/inspection-list');
                return;
            }
            this.sendLargeScreenData({
                data: item,
                isLight: true,
            });
        },
        initRTC() {
            //127.0.0.1：启动webrtc-streamer的设备IP
            this.webRtcServer = new WebRtcStreamer('rtcVideo', process.env.VUE_APP_WEBRTC_API_URL);
            this.webRtcServer2 = new WebRtcStreamer('rtcVideo2', process.env.VUE_APP_WEBRTC_API_URL);
            //需要查看的rtsp地址
            this.webRtcServer.connect('rtsp://admin:a1234567@192.168.1.64:554/h264/ch1/main/av_stream');
            this.webRtcServer2.connect('rtsp://admin:a1234567@192.168.1.64:554/h264/ch1/main/av_stream');
        },
        changeMonitor() {
            if (this.monitorContent === '视频') {
                this.monitorContent = '模型';
            } else {
                this.monitorContent = '视频';
            }
            // 发射custom自定义事件
            let custom = new CustomEvent('changeMonitor', {
                detail: {
                    monitorContent: this.monitorContent,
                },
            });
            document.dispatchEvent(custom);
        },
        bigScreenDatacChange(val) {
            this.waringList = (val.largeScreenAlertLog || []).map((item) => {
                switch (item.status) {
                    case 2:
                        item.statusDesc = '未处理';
                        break;
                    default:
                        break;
                }
                switch (item.alertLevel) {
                    case 1:
                        item.alertLevelDesc = '提醒通知';
                        break;
                    case 2:
                        item.alertLevelDesc = '轻微问题';
                        break;
                    case 3:
                        item.alertLevelDesc = '严重警告';
                        break;
                    default:
                        break;
                }
                return item;
            });
        },
        alertLevelDescColor(alertLevel) {
            let color = '';
            switch (alertLevel) {
                case 1:
                    color = 'rgba(57, 242, 168, 0.85)';
                    break;
                case 2:
                    color = 'rgba(250, 171, 2, 0.85)';
                    break;
                case 3:
                    color = 'rgba(244, 84, 84, 0.85)';
                    break;
                default:
                    break;
            }
            return color;
        },
        handleBeforeUnload(e) {
            // 可以在这里执行同步的轻量级操作（注意：不能是异步）
            console.log('页面即将关闭或刷新');
            setCloseAll();
        },
        sliderChange(val) {
            let param = {
                switchProp: '11',
                switchValue: val == 1 ? true : false,
            };
            this.sendLargeScreenData({
                data: param,
                isSlider: true,
            });
        },
        sendLargeScreenData(
            config = {
                data,
                isLight: false,
                isSlider: false,
            }
        ) {
            let params = {};
            if (config.isLight) {
                params.type = config.data.radioProp;
                params.switchStatus = config.data.switchValue ? '1' : '0';
                params.lightColor = config.data.radioValue == '白' ? '0' : '1';
            } else {
                params.type = config.data.switchProp;
                params.switchStatus = config.data.switchValue ? '1' : '0';
                params.lightColor = config.data.radioValue == '白' ? '0' : '1';
            }

            console.log('sendLargeScreenData@', params);
            setLargeScreenData(params)
                .then((res) => {})
                .catch((err) => {
                    // if (config.isLight) {
                    //     config.data.radioValue = '';
                    // } else if (config.isSlider) {
                    // } else {
                    //     config.data.switchValue = false;
                    // }
                });
        },
    },
};
</script>
